<template>
  <div>
    <b-row>
      <h3 class="mt-3 mb-3">
        2.
        <span>Billing Address</span>
      </h3>

      <p class="billing-description">
        Please place your shipping address below instead for any
        physical products.
      </p>
    </b-row>

    <b-row>
      <b-col sm="12" md="6">
        <b-form-group label="Country / Region *">
          <b-form-select v-model="country" :options="countries"></b-form-select>
        </b-form-group>
      </b-col>

      <b-col sm="12" md="6">
        <b-form-group label="State (optional)">
          <b-form-select v-model="state" :options="states"></b-form-select>
        </b-form-group>
      </b-col>

      <b-col cols="12">
        <b-form-group label="Address *">
          <b-form-input v-model="address" placeholder="House number and street name"></b-form-input>
        </b-form-group>
      </b-col>

      <b-col cols="12">
        <b-form-group>
          <b-form-input v-model="suburb" placeholder="Suburb"></b-form-input>
        </b-form-group>
      </b-col>

      <b-col sm="12" md="6">
        <b-form-group label="City *">
          <b-form-input v-model="city"></b-form-input>
        </b-form-group>
      </b-col>

      <b-col sm="12" md="6">
        <b-form-group label="ZIP *">
          <b-form-input v-model="zip"></b-form-input>
        </b-form-group>
      </b-col>
    </b-row>
  </div>
</template>

<script>
import { mapMutations } from "vuex";

export default {
  name: "AdressGroup",
  data() {
    return {
      country: "",
      state: "",
      suburb: "",
      city: "",
      zip: "",
      address: "",
      countries: ["Spain", "Venezuela"],
      states: ["Alabama", "Alaska"]
    };
  },
  methods: {
    ...mapMutations(["changeDataForm"])
  },
  watch: {
    country(newData) {
      this.changeDataForm({ key: "country", value: newData });
    },
    state(newData) {
      this.changeDataForm({ key: "state", value: newData });
    },
    address(newData) {
      this.changeDataForm({ key: "address", value: newData });
    },
    suburb(newData) {
      this.changeDataForm({ key: "suburb", value: newData });
    },
    city(newData) {
      this.changeDataForm({ key: "city", value: newData });
    },
    zip(newData) {
      this.changeDataForm({ key: "zip", value: newData });
    }
  }
};
</script>
