<template>
  <div>
    <b-row>
      <h3 class="mt-3 mb-3">
        3.
        <span>Your Order</span>
      </h3>
    </b-row>

    <b-row>
      <b-table-simple>
        <b-thead>
          <b-tr>
            <b-th>Product</b-th>
            <b-th class="text-right">Subtotal</b-th>
          </b-tr>
        </b-thead>
        <b-tbody>
          <b-tr v-for="product in products" :key="product.name">
            <b-td>{{ product.name }}</b-td>
            <b-td class="text-right">US${{ product.subtotal }}</b-td>
          </b-tr>
        </b-tbody>
        <b-tfoot>
          <b-tr>
            <b-td>Total</b-td>
            <b-td class="text-right">US${{ total }}</b-td>
          </b-tr>
        </b-tfoot>
      </b-table-simple>
    </b-row>

    <b-row class="mt-4">
      <b-container>
        <b-row class="justify-content-center">
          <b-col cols="6">
            <b-card bg-variant="light" class="text-center">
              <b-img
                v-bind="{
                  blank: true,
                  blankColor: '#777',
                  width: 75,
                  height: 75,
                  class: 'm1',
                }"
                center
                rounded="circle"
                alt="Circle image"
              ></b-img>
              <b-card-text class="mt-2">Paypal</b-card-text>
            </b-card>
          </b-col>

          <b-col cols="6">
            <b-card bg-variant="light" class="text-center">
              <b-img
                v-bind="{
                  blank: true,
                  blankColor: '#777',
                  width: 75,
                  height: 75,
                  class: 'm1',
                }"
                center
                rounded="circle"
                alt="Circle image"
              ></b-img>
              <b-card-text class="mt-2">Stripe</b-card-text>
            </b-card>
          </b-col>
        </b-row>
      </b-container>
    </b-row>

    <b-row class="mt-5">
      <b-button type="submit" variant="danger" size="lg" block
        >COMPLETE ORDER ></b-button
      >

      <b-form-group class="mt-3">
        <b-form-checkbox
          v-model="conditions"
          value="accepted"
          unchecked-value="not_accepted"
          >I understand Foundr’s money back guarantee</b-form-checkbox
        >
      </b-form-group>
    </b-row>
  </div>
</template>

<script>
import { mapMutations } from "vuex";

export default {
  name: "Order",
  data() {
    return {
      total: "1,997.00",
      conditions: "not_accepted",
      products: [{ name: "Instagram Domination Course", subtotal: "1,997.00" }],
    };
  },
  methods: {
    ...mapMutations(["changeDataForm"]),
  },
  watch: {
    conditions(newData) {
      this.changeDataForm({ key: "conditions", value: newData });
    },
  },
};
</script>
