<template>
  <div>
    <b-row>
      <h3 class="mt-3 mb-3">
        1.
        <span>Billing Information</span>
      </h3>
    </b-row>

    <b-row>
      <b-col cols="12">
        <b-form-group label="Email address *">
          <b-form-input v-model="email" placeholder="yo@example.com"></b-form-input>
        </b-form-group>
      </b-col>

      <b-col sm="12" md="6">
        <b-form-group label="First Name *">
          <b-form-input v-model="name" placeholder="Your first name"></b-form-input>
        </b-form-group>
      </b-col>

      <b-col sm="12" md="6">
        <b-form-group label="Tel. Number *">
          <b-form-input v-model="telephone" placeholder="Your phone number"></b-form-input>
        </b-form-group>
      </b-col>
    </b-row>
  </div>
</template>

<script>
import { mapMutations } from "vuex";

export default {
  name: "InformationGroup",
  data() {
    return {
      email: "",
      name: "",
      telephone: ""
    };
  },
  methods: {
    ...mapMutations(["changeDataForm"])
  },
  watch: {
    email(newData) {
      this.changeDataForm({ key: "email", value: newData });
    },
    name(newData) {
      this.changeDataForm({ key: "name", value: newData });
    },
    telephone(newData) {
      this.changeDataForm({ key: "telephone", value: newData });
    }
  }
};
</script>
